import React from "react";
import { Grid, Typography } from "@mui/material";

export default function Logo(): JSX.Element {
  const height = "100px";
  return (
    <Grid
      container
      sx={{ display: "flex", justifyContent: "center", alignItems: "center", height }}
    >
      <Grid item>
        <img
          className="football-icon"
          alt="logo"
          src={`${process.env.PUBLIC_URL}/assets/images/football.png`}
          height="100px"
          width="100px"
        />
      </Grid>
      <Grid item sx={{ ml: 1 }}>
        <Typography
          lineHeight={height}
          component="h1"
          fontSize="100px"
          textAlign="center"
          sx={{ userSelect: "none" }}
          color="#16B7EA"
          fontFamily="Grandstander"
        >
          FPL Zone
        </Typography>
      </Grid>
    </Grid>
  );
}
