import { Box, Modal } from "@mui/material";
import { useState } from "react";

interface ImageModalProps {
  image: string;
}

export default function ImageModal({ image }: ImageModalProps): JSX.Element {
  const [modalOpen, setModalOpen] = useState<boolean>(false);
  const imageUrl = `${process.env.PUBLIC_URL}/assets/images/screenshots/${image}.png`;

  return (
    <>
      <Box
        sx={{
          backgroundImage: `url(${imageUrl})`,
          backgroundSize: "cover",
          height: "100%",
          width: "100%",
          border: "1px solid black",
        }}
        onClick={() => setModalOpen(true)}
      />
      <Modal open={modalOpen} onClick={() => setModalOpen(false)}>
        <Box
          sx={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            boxShadow: 20,
          }}
        >
          <img src={imageUrl} alt="fpl-zone-screenshot" height="700px" />
        </Box>
      </Modal>
    </>
  );
}
